import { Dispatch, MouseEvent, SetStateAction } from "react"
import { MonochromeToggleButtonGroup } from "@rp/react/web/common/composite-components"
import { Box, ToggleButton } from "@rp/react/web/mui/core"
import { ViewList, ViewModule } from "@rp/react/web/mui/icons"

export type WatchlistsModeView = "card" | "list"

type Props = {
  modeView: WatchlistsModeView
  setModeView: Dispatch<SetStateAction<WatchlistsModeView>>
}

export const WatchlistsCardViewToggles = ({ modeView, setModeView }: Props) => {
  const handleChange = (_event: MouseEvent<HTMLElement>, newValue: string | null) => {
    if (newValue === "list") return setModeView("list")
    setModeView("card")
  }

  return (
    <Box role="group" sx={{ position: "absolute", top: 16, right: 24, zIndex: 1 }}>
      <MonochromeToggleButtonGroup
        exclusive
        value={modeView}
        onChange={handleChange}
        size={"large"}
      >
        <ToggleButton aria-label="Card view" value={"card"}>
          <ViewModule />
        </ToggleButton>
        <ToggleButton aria-label="List view" value={"list"}>
          <ViewList />
        </ToggleButton>
      </MonochromeToggleButtonGroup>
    </Box>
  )
}
